import React from 'react'
import {Link} from 'react-router-dom'

const Restaurants = (props) => {
    return (
        <div className='resto-card-wrapper'>
        {props.restaurant.map((resto)=>
        <Link style={{ textDecoration: 'none' }}
            to={{pathname:`/restaurant/:${resto.restaurant.name}`,
            state:{resto}
        }}>    
            <section className='resto-card'>
                 <div>
                    <img className='resto-image' src={!resto.restaurant.thumb?'https://images.unsplash.com/photo-1521305916504-4a1121188589?ixlib=rb-1.2.1&ixid=eyJhcHBfaWQiOjEyMDd9&auto=format&fit=crop&w=500&q=80':resto.restaurant.thumb} alt="Something"/>
                </div>
                <div>
                    <header className='resto-title'>{resto.restaurant.name}</header>
                    <header className='resto-timing'>{resto.restaurant.timings}</header>
                    <section className='resto-rating'>Rating: {resto.restaurant.user_rating.aggregate_rating}({resto.restaurant.user_rating.votes})</section>
                    <div className='resto-city'>{resto.restaurant.location.city}</div>
                </div>        
            </section> 
        </Link>
        )}
        </div>
    )
}


export default Restaurants
