import React,{useContext} from 'react';
import Card from '../Collections.Card/Card'
import {CollectionContext} from '../../../../ContextAPI/CollectionContext'

// const access_token=process.env.REACT_APP_ZOMATO_KEY

const Collections = () => {
    const [Collection, setCollection] = useContext(CollectionContext)
    if(Collection){
    return (
        <div className='collections-wrapper'>
            <div className="wrapper">
                <header className='collection-title'>Collections</header> 
                <header className='collection-subtitle'>Explore curated lists of top restaurants, cafes, pubs, and bars in Mumbai, based on trends</header>   
            </div>
            <div className="card-wrapper">
                    {Collection.map(collectionList =>
                        <Card collectionList={collectionList}/>            
                    )}                
            </div>
        </div>
    )}
    else{
        return <h1>Loading....</h1>
    }
}

export default Collections
