import React from 'react'
import {Link} from 'react-router-dom';


function Card(props) {
    const collection=props.collectionList.collection


        return (
            <div className="card">
            <Link  style={{ textDecoration: 'none' }}
            to={{pathname:`/collection/${collection.title}`,
            state:collection
            }}>
                <img className='collection-image' src={collection.image_url} alt=""/>
                <header className='collection-heading'>{collection.title}</header>
            </Link>
            </div>        
        )

}

export default Card
