import React,{useEffect,useState} from 'react';
import {useLocation} from 'react-router-dom';
import Restaurants from '../Restaurants/Restaurants';
import Loading from '../LoadingComponent/Loading';
import {Link} from 'react-router-dom';
import axios from 'axios';


const CollectionType = () => {
    let location=useLocation();
    const {description,image_url,res_count,title,collection_id}=location.state
    const [restaurant,setRestaurant]=useState([]);
    const [isLoading,setLoading]=useState(true);
    
    useEffect(() => {
        const config = { headers: {'user-key': process.env.REACT_APP_ZOMATO_API_KEY } }; 
        const fetchCollectionData=() => axios.get(`https://developers.zomato.com/api/v2.1/search?collection_id=${collection_id}`, config) 
        .then(res => { 
            setRestaurant(res.data.restaurants)
            setLoading(false)
        }); 
        
        fetchCollectionData()
    },[])
    
    if(!isLoading){
    return (
        <div>
            <Link to='/' style={{textDecoration: 'none',color:'black'}}>
                <header className='zomato-logo'>ZOMATO</header>
            </Link>            <section className='collectionTemplate'>
                <img className={'TemplateImage'}  src={image_url} alt="Mumbai Food"/>
                <header className='TemplateTitle'>{title}</header>
                <header className={"TemplateSubtitle"}>{description}</header>
                <span className="resto_count">{res_count} Place(s) in  &nbsp;<u> {title} </u> &nbsp; collection</span>
            </section>
            <Restaurants restaurant={restaurant} /> 
        </div>
    )}
    else{
        return <Loading />
    }
}

export default CollectionType
