import React from 'react'
import {useLocation} from 'react-router-dom';
import {Link} from 'react-router-dom' 

const Restaurant = (props) => {
    let location=useLocation();
    const resto=location.state.resto.restaurant
 
    return (
        <>
        <div className='restoScreen'>
            <Link to='/' style={{textDecoration: 'none',color:'black'}}>
                <header className='zomato-logo'>ZOMATO</header>
            </Link>            <hr className='style-one' />
            <div className="restoDetails">
                <div className="templateImage">
                    <img  className='restoFeaturedImage' src={resto.featured_image} alt="resto featured"/>
                </div>                
                <div className='titleFlex'>
                    <header className="restoTitle">{resto.name}</header>
                    
                    <span className="restoRating">
                        <span>&#11088;</span>{resto.user_rating.aggregate_rating} 
                        
                        <Link
                            to={{pathname:`/${resto.name}/:${resto.all_reviews_count}`,
                            state:{resto}
                        }}>
                        (All reviews {resto.all_reviews_count}) 
                        </Link>
                    
                    </span>
                </div>
                <div className='restoContentDetails'>
                    <div>
                        <div className="restoEstablishment">
                            {resto.establishment}
                        </div>
                        <div className="restoLocation">
                            Address: {resto.location.address}
                        </div>    
                        <div className="restoTimings">
                            Timings: {resto.timings}
                        </div>
                    </div>
                    <div>
                        <header className='restoHeader'>Call</header>
                        <a href={'tel:'+resto.phone_numbers.slice(0,14)} className="restoContact">
                            {resto.phone_numbers.slice(0,14)}
                        </a>
                        <header className='restoHeader'>Average Cost</header>
                        <div className="restoAverageCost">
                            {resto.currency}{resto.average_cost_for_two} (Approx)
                        </div>
                    </div>
                </div>
                <div className='restoCuisines'>
                <header className='restoHeader'>Cuisines</header>
                    {resto.cuisines.split(',').join(" / ")}
                </div>     
            </div>
        </div>
        </>
    )
}

export default Restaurant
